.features {
  background-image: url("../img/mask.png");
  background-color: $white;
  background-position: center -57px;
  background-repeat: no-repeat;
  background-size: cover;
}

.features__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__image {
  order: 2;
}

.features__pic--desktop {
  display: none;
}

.features__list {
  list-style: none;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: $dark-two;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  padding-left: 0;
  padding-top: 10px;
  margin: 0 auto;
  width: 260px;
  text-align: center;
}

.features__item {
  padding-top: 27px;
}

.features__header {
  font-size: 18px;
  line-height: 10px;
  font-weight: 600;
  color: $black;
  margin-bottom: 23px;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .features__image {
    display: block;
    order: initial;
  }

  .features {
    background-position: center -106px;
  }

  .features__wrapper {
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
  }
}

@media (min-width: $desktop-width) {
  .features {
    background-position: center -140px;
  }

  .features__pic--desktop {
    display: block;
  }

  .features__wrapper {
    flex-direction: column;
  }

  .features__image {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  .features__list {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  .features__header {
    font-size: 30px;
    line-height: 30px;
    font-weight: bold;
    width: 230px;
    margin: 0 auto;
    padding-top: 47px;
    padding-bottom: 60px;
  }


  .features__text {
    width: 241px;
    text-align: center;
    margin: 0 auto;
  }
}
