.contacts {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
}

.contacts__header {
  font-size: 24px;
  line-height: normal;
  font-weight: normal;
  color: $dark-two;
  width: 230px;
  padding-top: 50px;
  padding-bottom: 45px;
  margin: 0 auto;
  text-align: center;
}

.contacts__phone {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 250px;
}

.contacts__mail {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 250px;
}

.contacts__adress {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 250px;
  margin: 0 auto;
}

.contacts__text {
  font-size: 18px;
  line-height: normal;
  font-weight: bold;
  color: $black;
  margin-top: 0;
  margin-bottom: 15px;
}

.contacts__info {
  font-size: 24px;
  line-height: normal;
  font-weight: normal;
  color: $lipstick;
  margin-top: 0;
  margin-bottom: 30px;
}

.contacts__wrapper {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    flex-wrap: wrap;
    width: 550px;
  }

  .contacts__phone {
    width: 50%;
  }

  .contacts__mail {
    width: 50%;
  }
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    display: flex;
    box-sizing: border-box;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
  }

  .contacts__phone {
    width: 250px;
  }

  .contacts__mail {
    width: 250px;
  }

  .contacts__adress {
    width: 250px;
    margin: 0;
  }
}
