.effect {
  display: none;
}

@media (min-width: $tablet-width) {
  .effect {
    display: flex;
    box-sizing: border-box;
    padding-top: 60px;
  }

  .effect__image {
    background-image: url("../img/icon-timer.svg");
    width: 300px;
    height: 345px;
    background-repeat: no-repeat;
    position: relative;
  }

  .effect__image-label {
    font-size: 120px;
    line-height: 1;
    color: $lipstick;
    font-weight: bold;
    position: absolute;
    top: 76px;
    left: 50%;
    transform: translateX(-50%);
    border: 24px solid;
    border-radius: 50%;
    padding: 38px 30px;
  }

  .effect__wraper {
    width: 265px;
    padding-top: 100px;
    padding-left: 63px;
  }
}

@media (min-width: $desktop-width) {
  .effect {
    width: 100%;
    padding-left: 130px;
    padding-right: 130px;
    padding-bottom: 70px;
    background-color: $white-two;
  }

  .effect__wraper {
    width: 535px;
  }
}
