.download {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-color: $dark-two;
  background-size: contain;
  background-position: center;
  padding-top: 180px;
  position: relative;
}

.download__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 1;
  color: $white;
  display: flex;
  box-sizing: border-box;
  width: 225px;
  text-align: center;
  margin-bottom: 30px;
  position: absolute;
  top: 85px;
}

.download__image {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
}

.download__wrapper {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: $dark-two;
  margin-left: -20px;
  margin-right: -20px;
  padding-top: 30px;
  padding-bottom: 30px;
}

.download__button {
  text-decoration: none;
  font-size: 18px;
  line-height: 18px;
  text-transform: none;
  color: $white;
  font-weight: bold;
  padding-top: 14px;
  padding-bottom: 14px;
  width: 281px;
  text-align: center;
  border: 3px $white solid;
  border-radius: 30px;
  margin-bottom: 30px;
  display: block;
}

.download__button:hover {
  color: $lipstick;
  background-color: $white;
}

.download__button:focus {
  color: $lipstick-03;
  background-color: $white;
}

.download__text {
  font-size: 14px;
  line-height: 24px;
  text-transform: none;
  color: $white;
  font-weight: 300;
  width: 230px;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  text-align: center;
}

@media (min-width: $tablet-width) {
  .download {
    background-image: url("../img/bg-back-tablet@1x.jpg");
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-back-tablet@2x.jpg");
      background-size: 730px auto;
    }
  }

  .download__wrapper {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    position: relative;
    background: none;
    align-items: center;
    width: 45%;
    margin-left: 0;
    margin-right: 0;
  }

  .download__button {
    width: 300px;
    margin-bottom: 30px;
  }

  .download__list {
    margin-bottom: 30px;
  }

  .download__text {
    margin-bottom: 30px;
  }

  .download__image {
    order: 2;
    position: absolute;
    left: 30%;
    bottom: 0;
  }

  .download__title {
    font-size: 40px;
    line-height: 1;
    width: 195px;
    position: static;
  }
}

@media (min-width: $desktop-width) {
  .download {
    background-image: url("../img/bg-back-desktop@1x.jpg");
    background-position: center 0;
    background-size: cover;
  }

  .download__title {
    font-size: 50px;
    line-height: 1;
    width: 231px;
  }

  .download__image {
    order: 2;
    position: absolute;
    right: 120px;
    bottom: -1px;
    left: auto;
  }
}
