.feedback {
  text-align: center;
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.feedback__image {
  display: none;
}

.feedback__header {
  font-size: 18px;
  line-height: 30px;
  font-weight: bold;
  color: $black;
  padding-top: 50px;
  padding-bottom: 30px;
  margin: 0;
}

.feedback__comment {
  font-size: 14px;
  line-height: 30px;
  font-weight: 400;
  color: $dark-two;
  display: none;
}

.feedback__text {
  font-size: 14px;
  line-height: 30px;
  font-weight: normal;
  color: $dark-two;
}

@media (min-width: $tablet-width) {
  .feedback {
    width: 620px;
    margin: 0 auto;
  }

  .feedback__text {
    padding-top: 100px;
    margin: 0;
  }

  .feedback__comment {
    display: block;
    order: 3;
  }

  .feedback__header {
    order: 2;
  }
}

@media (min-width: $desktop-width) {
  .feedback__image {
    margin-top: 70px;
    display: block;
  }

  .feedback__text {
    padding-top: 60px;
  }
}
