.prices {
  min-height: 525px;
  background-image: url("../img/mask.png");
  background-color: white;
  background-position: center -53px;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 250px;
  padding-bottom: 100px;
}

.prices__wrapper {
  overflow: hidden;
  position: relative;
  min-height: 447px;
}

.prices__table {
  width: 300%;
  position: absolute;
  transform: translateX(-46%);
}

.prices__head-cell {
  background-color: $dark-two;
  color: $white;
  position: relative;
  width: 235px;

  &--hit {
    &::before {
      content: ("Хит");
      position: absolute;
      top: 21px;
      left: 8px;
      z-index: 1;
      transform: rotate(-45deg);
    }
  }
}

.prices__hit {
  position: absolute;
  top: 0;
  left: 0;
}

.prices__name {
  font-size: 18px;
  font-weight: bold;
  line-height: 1.67;
  text-align: center;
  color: $white;
  padding-top: 25px;
  padding-bottom: 10px;
}

.prices__value {
  font-size: 24px;
  font-weight: 300;
  line-height: 1.25;
  text-align: center;
  color: $white;
  padding-bottom: 25px;
}

.prices__inner-cell {
  border: 1px solid $white-two;
  background-color: $white;
  text-align: left;
  padding-left: 24px;
  position: relative;
  font-size: 14px;
  line-height: 30px;
  color: $dark-two;
  text-transform: uppercase;
}

.prices__pic {
  position: absolute;
  top: 20px;
  right: 20px;
}

.prices__side-cell {
  font-size: 18px;
  line-height: 1.67;
  text-align: center;
  color: $dark-two;
  border: 1px solid $white-two;
  background-color: $white;
  padding: 25px;
  width: 235px;
}

@media (min-width: $tablet-width) {
  .prices__wrapper {
    overflow: hidden;
  }

  .prices__servise {
    display: none;
  }

  .prices__table {
    width: 100%;
    transform: translateX(-1px);
  }

  .prices__inner-cell {
    text-align: center;
    padding-left: 0;
  }

  .prices__pic {
    position: static;
    top: 20px;
    right: 20px;
  }
}

@media (min-width: $desktop-width) {
  .prices__servise {
    display: none;
  }
}
